.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--dark-card);
  color: var(--dark-text-sec);
  flex: 1;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  font-size: 0.75rem;
  font-weight: 700;
  transition: background 150ms ease-in-out;
}
.card:hover {
  background: var(--dark-card-hover);
}
.card.large {
  border-top: 0.3125rem solid;
  text-align: center;
  /* Gradient borders aren't supported by using CSS (https://www.geeksforgeeks.org/gradient-borders) */
}
.card.large.facebook {
  border-color: var(--facebook);
}
.card.large.twitter {
  border-color: var(--twitter);
}
.card.large.instagram {
  border-top: none;
  position: relative;
  overflow: hidden;
  padding: 1.875rem;
}
.card.large.instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.1875rem;
  width: 100%;
  background: linear-gradient(45deg, var(--instagram-start), var(--instagram-end));
  mask-composite: exclude;
}
.card.large.youtube {
  border-color: var(--youtube);
}
.card.large .platform {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.75rem;
  height: 1.25rem;
}
.card.large .platform img {
  margin-right: 8px;
}
.card.large .content {
  margin-bottom: 1.5625rem;
}
.card.large .content .count {
  font-size: 3.5rem;
}
.card.large .content .label {
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.3125rem;
}
.card.small {
  display: grid;
  /*margin-bottom: rem(16);*/
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 1.40625rem;
  justify-content: space-between;
  align-items: center;
  /*
  & .platform {
      display: flex;
      align-items: center;
      justify-content: space-between;
  }*/
}
.card.small .metric {
  font-size: 0.875rem;
}
.card.small .count {
  font-size: 2rem;
  color: var(--dark-text-primary);
  margin-bottom: 0;
}
.card.small .count, .card.small .change {
  align-self: end;
}
.card.small img, .card.small .change {
  justify-self: end;
}
.card p {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}
.card .count {
  color: var(--dark-text-primary);
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card .change {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card .change img {
  margin-right: 0.25rem;
}
.card .change.increase {
  color: var(--change-up);
}
.card .change.decrease {
  color: var(--change-down);
}